
.navbar-brand h1 {
    margin-top:9px;
    padding-top:5px;
    font-size: 25px;	
    color:#fff;	
    text-transform:uppercase;
    font-weight:600;
}

.menu {
    font-size:12px;
    float: right;
    text-transform:uppercase;
    font-weight:600;	
}

.menu ul.nav-tabs li {
    color:#fff;	
}

.nav-tabs {
    border-bottom: 0;
}
.nav-tabs > li {
    float: left;
    margin-bottom: 0;
}

.nav-tabs > li > a {	
    color:#fff;
    padding:30px;
}

.nav-tabs > li > a:hover {
    background:#fff;	
} 

.nav-tabs > li a.active {
    color: #555;
    cursor: default;
    background-color: #fff;
    border: 1px solid #fff;
    border-bottom-color:#fff;
}

.navbar-default {
    background-color: #65AAF0;
    border-color:#65AAF0;
}